import * as React from 'react';
import isAbsoluteUrl from 'is-absolute-url';

const externalProps = {
  target: '_blank',
  rel: 'noopener noreferrer',
};

const AnchorTag = ({ children, href, ...props }) => {
  if (!children) return null;
  let isExternal = false;

  if (href[0] === '#') {
    isExternal = false;
  } else if (href.slice(0, 4) === 'mdn:') {
    href = `https://developer.mozilla.org/en-US/docs/${href.slice(4)}`;
    isExternal = true;
  } else if (isAbsoluteUrl(href)) {
    isExternal = true;
  }

  return (
    <a href={href} {...(isExternal ? externalProps : null)} {...props}>
      {children}
    </a>
  );
};

export default AnchorTag;
