import config from '../../config';
import pages from '../../pages';

export function calculateTreeData(edges) {
  return calculateTreeSubsectionData('', edges);
}

export function calculateTreeSubsectionData(rootSlug, edges) {
  const orderedNodes = getLayoutNodes(edges);
  const tree = { items: [] };
  let parents = [];

  for (let { slug, pageTitle, title } of orderedNodes) {
    const node = { items: [], slug, pageTitle, title };
    const slugRootText = slug.slice(0, rootSlug.length);
    let isChild = false;
    let parentNode = null;

    if (slug.length <= rootSlug.length || slugRootText !== rootSlug) {
      continue;
    }

    if (slug === '/') {
      tree.items.push(node);
      parents.length = 0;
      continue;
    }

    while ((parentNode = parents[0])) {
      const pslug = parentNode.slug;
      const parentSlug = pslug[pslug.length - 1] === '/' ? pslug : `${pslug}/`;
      const slugBeginning = slug.slice(0, parentSlug.length);

      if (slugBeginning === parentSlug) {
        isChild = true;

        parentNode.items.push(node);
        parents.unshift(node);
        break;
      } else {
        parents.shift();
      }
    }

    if (!isChild) {
      tree.items.push(node);
      parents.push(node);
    }
  }

  return tree;
}

export function getLayoutNodes(edges) {
  if (!edges) return [];

  const nodesBySlug = {};
  for (const { node } of edges) {
    const { slug } = node.fields;
    if (slug) {
      nodesBySlug[slug] = node;
    }
  }

  const nav = [];
  for (let { slug, title } of pages) {
    if (slug.length > 1 && slug[slug.length - 1] === '/') {
      slug = slug.slice(0, slug.length - 1);
    }
    const node = nodesBySlug[slug];
    if (!slug) {
      throw new Error('node is missing slug');
    }
    if (!node) {
      throw new Error(`slug ${slug} not found`);
    }

    const { fields = {} } = node;
    const { title: pageTitle } = fields;
    if (title === undefined) title = pageTitle;
    if (config.sidebar.ignoreIndex && slug === '/') continue;

    nav.push({ slug, pageTitle, title });
  }

  return nav;
}
