import React from 'react';
import cx from 'classnames';
import { StaticQuery, graphql } from 'gatsby';
import GitHubButton from 'react-github-btn';
import HelpSvg from '../images/help.svg';
import LeftSidebar from '../LeftSidebar';
import styles from './Header.module.css';
import HeaderNavbar from './HeaderNavbar';

const Header = () => (
  <StaticQuery
    query={graphql`
      query headerTitleQuery {
        site {
          siteMetadata {
            headerTitle
            githubUrl
            helpUrl
            logo {
              link
              image
            }
            headerLinks {
              link
              text
            }
          }
        }
      }
    `}
    render={({ site }) => <Contents site={site} />}
  />
);

const Contents = ({ site }) => {
  const {
    siteMetadata: { headerTitle, githubUrl, helpUrl, logo, headerLinks },
  } = site;
  let githubButtonFragment, helpIconFragment;

  const headerLinksFragment = headerLinks.map((link, key) => {
    if (link.link !== '' && link.text !== '') {
      return (
        <li key={key}>
          <a
            href={link.link}
            target="_blank"
            rel="noopener noreferrer"
            dangerouslySetInnerHTML={{ __html: link.text }}
          />
        </li>
      );
    }
  });

  if (helpUrl !== '') {
    helpIconFragment = (
      <li>
        <a href={helpUrl}>
          <HelpSvg alt={'Help icon'} />
        </a>
      </li>
    );
  }

  if (githubUrl !== '') {
    githubButtonFragment = (
      <>
        <li className={styles.divider}></li>
        <li className={styles.githubBtn}>
          <GitHubButton href={githubUrl} data-show-count="true" aria-label="Star on GitHub">
            Star
          </GitHubButton>
        </li>
      </>
    );
  }

  return (
    <>
      <nav className={cx('navbar', 'navbar-expand-lg', 'navbar-default', styles.navbarDefault)}>
        <HeaderNavbar target={'navbar'} {...{ headerTitle, logo }} />
        <div id="navbar" className={cx('navbar-collapse', 'collapse', styles.navbarCollapse)}>
          <div className={styles.sidebarContainer}>
            <LeftSidebar />
            <hr />
          </div>
          <ul className={cx('nav', 'navbar-nav', 'navbar-right', styles.navBarULRight)}>
            {headerLinksFragment}
            {helpIconFragment}
            {githubButtonFragment}
          </ul>
        </div>
      </nav>
    </>
  );
};

export default Header;
