import React from 'react';
import cx from 'classnames';
import ImageLogo from '../images/ImageLogo';
import Link from '../Link';
import styles from './HeaderBrand.module.css';

const textLogo = require('../images/logo-text.png');

const HeaderBrand = ({ className, headerTitle, logo }) => {
  const finalLogoLink = logo.link !== '' ? logo.link : '/';
  let logoImageFragment, titleFragment;

  if (logo.image !== '') {
    logoImageFragment = (
      <img className={cx('img-responsive', styles.imageLogo)} src={logo.image} alt={'Image Logo'} />
    );
  } else {
    logoImageFragment = <ImageLogo />;
  }

  if (headerTitle) {
    titleFragment = (
      <div className={styles.headerTitle} dangerouslySetInnerHTML={{ __html: headerTitle }} />
    );
  } else {
    titleFragment = (
      <div className={styles.headerTitle}>
        <img className={styles.textLogo} src={textLogo} alt={'Text Logo'} />
      </div>
    );
  }

  return (
    <Link
      to={finalLogoLink}
      className={cx('navbar-brand', styles.navBarBrand, className)}
      tabIndex="-1"
    >
      {logoImageFragment}
      {titleFragment}
    </Link>
  );
};

export default HeaderBrand;
