import React, { useState } from 'react';
import config from '../../../config';
import { calculateTreeData } from '../../utils/layout';
import TreeNode from './TreeNode';

const Tree = ({ edges }) => {
  const [treeData] = useState(() => {
    return calculateTreeData(edges);
  });
  const [collapsed, setCollapsed] = useState({});
  const toggle = (url) => {
    setCollapsed({
      ...collapsed,
      [url]: !collapsed[url],
    });
  };

  return (
    <TreeNode
      collapsed={collapsed}
      isRoot={true}
      setCollapsed={toggle}
      showFrontLine={!!config.sidebar.frontLine}
      {...treeData}
    />
  );
};

export default Tree;
