import React from 'react';
import cx from 'classnames';
import AnchorTag from './AnchorTag';
import Code from './Code';
import CodeBlock from './CodeBlock';
import Pre from './Pre';
import styles from './styles.module.css';

function getId(props) {
  const { children } = props;
  if (Array.isArray(children)) {
    return children[0].href;
  } else {
    return children.replace(/\s+/g, '').toLowerCase();
  }
}

/* eslint-disable react/display-name */
export default {
  a: (props) => <AnchorTag {...props} />,
  // TODO add `blockquote`
  code: CodeBlock,
  h1: ({ className, ...props }) => (
    <h1 id={getId(props)} {...props} className={cx(className, styles.h1)} />
  ),
  h2: ({ className, ...props }) => (
    <h2 id={getId(props)} {...props} className={cx(className, styles.h2)} />
  ),
  h3: ({ className, ...props }) => (
    <h3 id={getId(props)} {...props} className={cx(className, styles.h3)} />
  ),
  h4: ({ className, ...props }) => (
    <h4 id={getId(props)} {...props} className={cx(className, styles.h4)} />
  ),
  h5: ({ className, ...props }) => (
    <h5 id={getId(props)} {...props} className={cx(className, styles.h5)} />
  ),
  h6: ({ className, ...props }) => (
    <h6 id={getId(props)} {...props} className={cx(className, styles.h6)} />
  ),
  img: ({ className, ...props }) => <img {...props} className={cx(className, styles.img)} />,
  inlineCode: (props) => <Code {...props} />,
  ol: ({ className, ...props }) => <ol {...props} className={cx(className, styles.ol)} />,
  p: ({ className, ...props }) => <p {...props} className={cx(className, styles.p)} />,
  pre: Pre,
  table: ({ className, ...props }) => <table {...props} className={cx(className, styles.table)} />,
  ul: ({ className, ...props }) => <ul {...props} className={cx(className, styles.ul)} />,
};
