import React from 'react';
import cx from 'classnames';
import config from '../../../config';
import ClosedSvg from '../images/ClosedSvg';
import OpenedSvg from '../images/OpenedSvg';
import Link from '../Link';
import styles from './TreeNode.module.css';

const TreeNode = ({
  collapsed,
  isRoot,
  isRootItem = false,
  items,
  location,
  setCollapsed,
  showFrontLine,
  slug,
  title,
}) => {
  const isCollapsed = collapsed[slug];
  const collapse = () => {
    setCollapsed(slug);
  };
  const hasChildren = items.length !== 0;
  const active =
    location &&
    (location.pathname === slug || location.pathname === config.gatsby.pathPrefix + slug);
  const className = cx(
    styles.item,
    active && styles.active,
    hasChildren && styles.hasChildren,
    isRoot && (showFrontLine ? styles.showFrontLine : styles.hideFrontLine),
    isRoot && styles.isRoot,
    isRootItem && styles.isRootItem
  );

  return (
    <li className={className}>
      {!config.sidebar.frontLine && title && hasChildren ? (
        <button onClick={collapse} className={styles.collapser}>
          {!isCollapsed ? <OpenedSvg /> : <ClosedSvg />}
        </button>
      ) : null}

      {title && <Link to={slug}>{title}</Link>}

      {!isCollapsed && hasChildren ? (
        <ul>
          {items.map((item) => (
            <TreeNode
              key={item.slug}
              setCollapsed={setCollapsed}
              collapsed={collapsed}
              isRoot={false}
              isRootItem={isRoot}
              {...item}
            />
          ))}
        </ul>
      ) : null}
    </li>
  );
};

export default TreeNode;
