import React from 'react';
import cx from 'classnames';
import { HeaderBrand, HeaderSearch } from './';
import styles from './HeaderNavbar.module.css';

const HeaderNavbar = ({ headerTitle, logo, target }) => (
  <div className={cx('navbar-header', styles.headerNavbar)}>
    <HeaderBrand className={styles.headerBrand} {...{ headerTitle, logo }} />
    <HeaderSearch className={styles.headerSearch} />
    <button
      type="button"
      className={cx('navbar-toggle', 'collapsed', styles.navbarToggle)}
      data-toggle="collapse"
      data-target={`#${target}`}
      aria-expanded="false"
      aria-controls={target}
    >
      <span className={'sr-only'}>Toggle navigation</span>
      <span className={cx('icon-bar', styles.iconBar)}></span>
      <span className={cx('icon-bar', styles.iconBar)}></span>
      <span className={cx('icon-bar', styles.iconBar)}></span>
    </button>
  </div>
);

export default HeaderNavbar;
