import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import config from '../../config';
import styles from './RightSidebar.module.css';

const SidebarLayout = (props) => (
  <StaticQuery
    query={graphql`
      query {
        allMdx {
          edges {
            node {
              fields {
                slug
              }
              tableOfContents
            }
          }
        }
      }
    `}
    render={({ allMdx }) => <Contents allMdx={allMdx} {...props} />}
  />
);

const Contents = ({ allMdx, location }) => {
  let contentsFragment, finalNavItems;

  if (allMdx.edges !== undefined && allMdx.edges.length > 0) {
    for (const item of allMdx.edges) {
      if (item === undefined) continue;

      const matchesSlug =
        location.pathname === item.node.fields.slug ||
        location.pathname === config.gatsby.pathPrefix + item.node.fields.slug;

      if (matchesSlug && item.node.tableOfContents.items) {
        finalNavItems = item.node.tableOfContents.items.map((innerItem, index) => (
          <li key={index} className={styles.listItem}>
            <a href={innerItem.url}>{innerItem.title}</a>
          </li>
        ));
      }
    }
  }

  if (finalNavItems && finalNavItems.length) {
    contentsFragment = (
      <ul className={styles.rightSideBarUL}>
        <div className={styles.rightSideTitle}>CONTENTS</div>
        {finalNavItems}
      </ul>
    );
  } else {
    contentsFragment = <ul></ul>;
  }

  return <aside className={styles.sidebar}>{contentsFragment}</aside>;
};

export default SidebarLayout;
