import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import isAbsoluteUrl from 'is-absolute-url';

const externalProps = {
  target: '_blank',
  rel: 'noopener noreferrer',
};

const Link = ({ children, to, activeClassName, partiallyActive, ...props }) => {
  let isExternal = false;
  let useLink = false;

  if (to[0] === '#') {
    isExternal = false;
    useLink = false;
  } else if (isAbsoluteUrl(to)) {
    isExternal = true;
    useLink = false;
  } else {
    isExternal = false;
    useLink = true;
  }

  if (useLink) {
    return (
      <GatsbyLink
        to={to}
        activeClassName={activeClassName}
        partiallyActive={partiallyActive}
        {...props}
      >
        {children}
      </GatsbyLink>
    );
  } else {
    return (
      <a href={to} {...(isExternal ? externalProps : null)} {...props}>
        {children}
      </a>
    );
  }
};

export default Link;
