import React from 'react';
import cx from 'classnames';
import { connectSearchBox } from 'react-instantsearch-dom';
import SearchSvg from '../images/SearchSvg';
import styles from './InputForm.module.css';

export default connectSearchBox(({ collapse, focus, refine, ...rest }) => {
  const preventSubmit = (e) => {
    e.preventDefault();
  };
  const inputClassName = cx(
    styles.searchInput,
    collapse ? styles.collapse : styles.expand,
    collapse && focus && styles.focus
  );

  return (
    <form className={styles.formElement} onSubmit={preventSubmit}>
      <SearchSvg className={styles.searchIcon} />
      <div className={styles.searchWrapper}>
        <div
          className={inputClassName}
          type="text"
          placeholder="Search"
          aria-label="Search"
          onChange={(e) => refine(e.target.value)}
          {...rest}
        />
      </div>
    </form>
  );
});
