import React, { useState } from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { calculateTreeSubsectionData } from '../utils/layout';
import Link from './Link';

const InThisSection = (props) => (
  <StaticQuery
    query={graphql`
      query {
        allMdx {
          edges {
            node {
              fields {
                slug
                title
              }
            }
          }
        }
      }
    `}
    render={({ allMdx }) => <Contents allMdx={allMdx} {...props} />}
  />
);

const Contents = ({ allMdx, slug = '' }) => {
  const [treeData] = useState(() => {
    return calculateTreeSubsectionData(slug, allMdx.edges);
  });
  const items = treeData.items.map(({ slug, pageTitle, title }) => (
    <li key={slug}>
      <Link to={slug}>{title}</Link>
      {pageTitle && pageTitle !== title && <span>: {pageTitle}</span>}
    </li>
  ));

  return (
    <>
      <h4>In This Section:</h4>
      <ul className={'inThisSection'}>{items}</ul>
    </>
  );
};

export default InThisSection;
