import React, { useState, createRef } from 'react';
import algoliasearch from 'algoliasearch/lite';
import cx from 'classnames';
import {
  InstantSearch,
  Index,
  Hits,
  Configure,
  connectStateResults,
} from 'react-instantsearch-dom';
import config from '../../../config.js';
import styles from './AlgoliaSearch.module.css';
import { InputForm, PoweredBy, hitComps, useClickOutside } from './';

const Root = ({ className, ...props }) => <div className={cx(styles.root, className)} {...props} />;

const Results = connectStateResults(({ searchState: state, searchResults: res, children }) =>
  res && res.query && res.nbHits > 0 ? children : `No results for '${state.query}'`
);

export default function AlgoliaSearch({ collapse, indices }) {
  const ref = createRef();
  const [query, setQuery] = useState('');
  const [focus, setFocus] = useState(false);
  const searchClient = algoliasearch(
    config.header.search.algoliaAppId,
    config.header.search.algoliaSearchKey
  );
  const showResults = query.length > 0 && focus;
  const hitsClassName = cx(
    styles.hitsWrapper,
    showResults ? styles.showResults : styles.hideResults
  );

  useClickOutside(ref, () => setFocus(false));

  return (
    <InstantSearch
      searchClient={searchClient}
      indexName={indices[0].name}
      onSearchStateChange={({ query }) => setQuery(query)}
      root={{ Root, props: { ref } }}
    >
      <InputForm onFocus={() => setFocus(true)} {...{ collapse, focus }} />
      <div className={hitsClassName}>
        {indices.map(({ name, hitComp }) => {
          return (
            <Index key={name} indexName={name}>
              <Results>
                <Hits hitComponent={hitComps[hitComp](() => setFocus(false))} />
              </Results>
            </Index>
          );
        })}
        <PoweredBy />
      </div>
      <Configure hitsPerPage={5} />
    </InstantSearch>
  );
}
