import React from 'react';
import cx from 'classnames';
import config from '../../../config.js';
import AlgoliaSearch from '../AlgoliaSearch';
import DocSearch from '../DocSearch';
import styles from './HeaderSearch.module.css';

const HeaderSearch = ({ className }) => {
  const isAlgoliaSearchEnabled = !!(
    config.header.search &&
    config.header.search.enabled &&
    config.header.search.algoliaAppId
  );
  const isDocSearchEnabled = !!(
    config.header.search &&
    config.header.search.enabled &&
    config.header.search.algoliaApiKey &&
    config.header.search.indexName
  );

  let searchIndices = [];
  if (isAlgoliaSearchEnabled && config.header.search.indexName) {
    searchIndices.push({
      name: `${config.header.search.indexName}`,
      title: `Results`,
      hitComp: `PageHit`,
    });
  }

  let searchFragment;

  if (isAlgoliaSearchEnabled) {
    searchFragment = <AlgoliaSearch collapse indices={searchIndices} />;
  } else if (isDocSearchEnabled) {
    searchFragment = <DocSearch />;
  } else {
    return null;
  }

  return <div className={cx(styles.searchWrapper, className)}>{searchFragment}</div>;
};

export default HeaderSearch;
