import React, { useEffect, useRef } from 'react';
import config from '../../config.js';
import SearchSvg from './images/SearchSvg';
import styles from './DocSearch.module.css';

const DocSearch = () => {
  const search = useRef(null);
  const inputEl = useRef(null);
  const inputSelector = `.${styles.searchInput}`;

  useEffect(() => {
    const { algoliaApiKey: apiKey, indexName, debug, autocompleteOptions } = config.header.search;
    if (!global.docsearch) return;

    search.current = global.docsearch({
      apiKey,
      indexName,
      inputSelector,
      debug,
      autocompleteOptions,
    });

    return () => search.current.autocomplete.autocomplete.destroy();
  }, []);

  const preventSubmit = (event) => {
    event.preventDefault();
  };

  const handleKeyDown = (event) => {
    const { altKey, keyCode, shiftKey } = event;

    if (keyCode === 27 && !(shiftKey || altKey)) {
      // ESC
      const autocomplete = search.current.autocomplete.autocomplete;
      const existingVal = autocomplete.getVal();
      if (existingVal) {
        autocomplete.setVal('');
      } else {
        inputEl.current.blur();
      }
      event.preventDefault();
      event.stopPropagation();
    }
  };

  return (
    <form className={styles.formElement} onSubmit={preventSubmit}>
      <SearchSvg className={styles.searchIcon} />
      <div className={styles.searchWrapper}>
        <input
          className={styles.searchInput}
          type="text"
          placeholder="Search"
          aria-label="Search"
          onKeyDown={handleKeyDown}
          ref={inputEl}
        />
      </div>
    </form>
  );
};

export default DocSearch;
